import "core-js/modules/es.array.push.js";
import { defineComponent, ref, computed, reactive } from 'vue';
import { useStore } from 'vuex';
import { validatorName, validatorPhone } from '@/helpers/validators';
import UsersStorageManager from '@/services/UsersStorageManager';
export default defineComponent({
  name: 'DialogAddUser',
  props: {
    isDialogVisible: {
      type: Boolean,
      default: false
    }
  },
  setup(_, {
    emit
  }) {
    const dialogOptions = {
      dialogTitle: 'Добавить пользователя',
      formLabelWidth: '120px',
      labelNameItem: 'Имя',
      labelPhoneItem: 'Телефон',
      labelChiefItem: 'Начальник',
      selectChiefPlaceholder: 'Выберити начальника',
      buttonCancelLabel: 'Отмена',
      buttonConfirmLabel: 'Подтвердить'
    };
    const usersStorageManager = UsersStorageManager.getInstance();
    const store = useStore();
    const ruleFormRef = ref();
    const ruleForm = reactive({
      name: '',
      phone: '',
      chief: ''
    });
    const rules = reactive({
      name: [{
        validator: validatorName,
        trigger: 'blur'
      }],
      phone: [{
        validator: validatorPhone,
        trigger: 'blur'
      }]
    });
    const clearForm = () => {
      if (!ruleFormRef.value) return;
      ruleFormRef.value.resetFields();
    };
    const cancelDialog = () => {
      emit('closeDialog');
      clearForm();
    };
    const createSelectData = data => data.reduce((acc, item) => {
      if (item.children.length > 0) {
        return [...acc, {
          label: item.id,
          value: item.id
        }, ...createSelectData(item.children)];
      }
      return [...acc, {
        label: item.id,
        value: item.id
      }];
    }, []);
    const findChildren = (data, id) => data.reduce((acc, tableData) => {
      const {
        id: currentId,
        children: currentChildren
      } = tableData;
      if (acc) {
        return acc;
      }
      if (currentId === id) {
        return currentChildren;
      }
      if (currentChildren.length > 0) {
        return findChildren(currentChildren, id);
      }
      return acc;
    }, null);
    const addUser = async formEl => {
      if (!formEl) return;
      const isValid = await formEl.validate(valid => {
        if (valid) {
          const data = ruleForm.chief === '' ? store.state.tableData : findChildren(store.state.tableData, ruleForm.chief);
          const dataLength = data.length;
          data.push({
            id: `${ruleForm.name}-${ruleForm.phone}-${dataLength}`,
            phone: Number(ruleForm.phone),
            name: ruleForm.name,
            children: []
          });
        }
      });
      if (!isValid) return;
      cancelDialog();
      usersStorageManager.saveChangeInLocalStorage(store.state.tableData);
    };
    const selectData = computed(() => {
      const data = createSelectData(store.state.tableData);
      return data;
    });
    return {
      dialogOptions,
      ruleFormRef,
      ruleForm,
      rules,
      selectData,
      cancelDialog,
      addUser
    };
  }
});